<template>
	<view class="ma_ticket"  >
		<view class="swiper-tab">  
			<view class="swiper-tab-list" :class="currentTab == 0?'on':''" @click="selectTab(0)">有效礼券</view>  
			<view class="swiper-tab-list" :class="currentTab == 1?'on':''" @click="selectTab(1)">过期礼券</view>   
		</view>  
		
		<view class="ticket-list" v-if="hasTicket">
			<view class="coupon-item" :class="!currentTab==0?'h':''" v-for="(item, index) in tickets">
				<view class="coupon-item-left">
					<view class="coupon-item-info">
						<view class="coupon-item-money">¥{{ item.gprice }}</view>
						<view class="coupon-item-ask">{{maxPrice == 0?'无门槛':'满' + maxPrice + '元可用'}}</view>
					</view>
					<view class="coupon-item-content">
						<view class="coupon-left-top">
							<view class="coupon-item-name">{{ item.gprice }}元代金券</view>
							<view class="coupon-item-tip">订单结算支付时可用</view>
						</view>
						<view class="coupon-left-bottom">
							<view class="coupon-item-time">有效期至 {{ $u.timeFormat(item.gend_date, 'yyyy-mm-dd hh:MM:ss')}}</view>
						</view>
					</view>
				</view>

				<view class="coupon-item-right" v-if="currentTab==0">
					<view class="coupon-item-botton" @click="userEvent">
						去使用
					</view>
				</view>
			</view>
		</view>
		
		<view class="ticket-list" v-if="!hasTicket">
			<view class="address">
			  <view class="address_a">
				  <view class="img" style="background-image: url('http://m.qpic.cn/psb?/V13t2G4i0uprYL/uYo8U.oo0vWONsW93wC*ppXK6ZFdHOgOlSEiEbXukxY!/b/dFIBAAAAAAAA&bo=OQKmAQAAAAADB74!&rf=viewer_4'); width: 569rpx;height: 422rpx;"></view>
				  <view class="img2">暂无礼券</view>
			  </view>
			</view>
		</view>
		
	</view>
</template>

<script>
	import { IsLogin } from '@/api/member'
	import { TicketList } from '@/api/ticket'
	
	export default {
		name: "ticket",
		data() {
			return {
				maxPrice: 0,
				currentTab: 0,
				tickets: [],
				hasTicket: false,
			}
		},
		created() {
			var that = this
			this.isLogin = IsLogin() ? true : false
			if (!this.isLogin) {
				uni.navigateTo({
					url: '/pages/login/login'
				})
				return
			} else {
				TicketList({'page': this.page, 'limit': this.limit, 'type': this.currentTab}, (res) => {
					if (res.code !== 0) {
						uni.showModal({
							title: '错误提示',
							content: '网络异常，请稍后重试',
							showCancel: false
						})
						return 
					}
					if(!res.data.list || res.data.list.length <= 0) {
						that.hasTicket = false
					} else{
						that.hasTicket = true
						that.tickets = res.data.list
					}
				})
			}
			
		},
		methods:{
			userEvent(){
				this.$emit('onClick')
			},
			link(url) {
				uni.navigateTo({
					url,
				})
			},
			selectTab(id) {
				var that = this
				this.currentTab = id
				TicketList({'page': this.page, 'limit': this.limit, 'type': id}, (res) => {
					if (res.code !== 0) {
						uni.showModal({
							title: '错误提示',
							content: '网络异常，请稍后重试',
							showCancel: false
						})
						return 
					}
					if(!res.data.list || res.data.list.length <= 0) {
						that.hasTicket = false
					} else{
						that.hasTicket = true
						that.tickets = res.data.list
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	
	.ma_ticket {
	  font-family: PingFang SC;
	  color: rgba(0, 21, 62, 1);
	}
	
	.swiper-tab {
	  background: #fff;
	  z-index: 9;
	  position: fixed;
	  width: 100%;
	  border-bottom: 1rpx solid #dee3f0;
	  text-align: center;
	  line-height: 80rpx;
	}
	
	.swiper-tab-list {
	  display: inline-block;
	  width: 50%;
	  color: rgba(0, 21, 62, 1);
	  font-size: 32rpx;
	  font-weight: 500;
	}
	
	.on {
	  color: #0D5EFF;
	  border-bottom: 4rpx solid #0d5eff;
	}
	
	.address_a{
	  width: 650rpx;
	  margin: auto;
	}
	
	.address{
	  width: 100%;
	  font-family:PingFang SC;
	  color:rgba(0,21,62,1);
	}
	.img{
	   background-repeat: no-repeat;
	   background-size: cover;
	   margin: auto;
	   margin-top: 150rpx;
	}
	.img2{
		font-size:38rpx;
		font-weight:400;
		margin-top: 65rpx;
		text-align: center;
	}
	
	.ticket-list {
		position: relative;
		top:80rpx;
		display: block;
		width: 100%;
		overflow: hidden;
	}
	
	.coupon-item {
		background: #fff;
		padding: 20rpx;
		border-radius: 20rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-top: 20rpx;

		&.h {
			filter: grayscale(100%);
		}

		.coupon-item-left {
			display: flex;
			align-items: center;

			.coupon-item-info {
				width: 170rpx;
				height: 170rpx;
				background-image: url("data:image/png;base64,");
				background-repeat: no-repeat;
				background-size: 100% 100%;
				color: #fff;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				flex-shrink: 0;

				.coupon-item-money {
					font-size: 50rpx;
					font-weight: bold;
				}

				.coupon-item-money::first-letter {
					font-size: 24rpx;
					font-weight: normal;
				}

				.coupon-item-ask {
					font-size: 26rpx;
				}
			}

			.coupon-item-content {
				color: #333;
				font-size: 28rpx;
				height: 160rpx;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				margin-left: 20rpx;
				flex-shrink: 0;

				.coupon-left-top {
					.coupon-item-name {
						font-size: 30rpx;
						font-weight: bold;
					}

					.coupon-item-tip {
						font-size: 24rpx;
						color: #888888;
					}
				}

				.coupon-left-bottom {
					.coupon-item-time {
						font-size: 24rpx;
						color: #888888;
					}
				}
			}
		}

		.coupon-item-right {
			.coupon-item-botton {
				background: #ff4131;
				color: #fff;
				border-radius: 50rpx;
				padding: 12rpx 24rpx;
				font-size: 24rpx;
				flex-shrink: 0;
			}
		}
	}
</style>